import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '', redirectTo: 'org', pathMatch: 'full',
  },
  {
    path: 'org', loadChildren: () => import('@admin/org/org.module').then(module => module.OrgModule),
  },
  {
    path: 'agendas', loadChildren: () => import('@admin/agendas/agendas.module').then(module => module.AgendasModule),
  },
  {
    path: 'feedbacks', loadChildren: () => import('@admin/feedbacks/feedbacks.module').then(module => module.FeedbacksModule),
  },
  {
    path: 'nda', loadChildren: () => import('@admin/nda/nda.module').then(module => module.NdaModule),
  },
  {
    path: 'users', loadChildren: () => import('@admin/users/users.module').then(module => module.UsersModule),
  },
  {
    path: 'visitors', loadChildren: () => import('@admin/visitors/visitors.module').then(module => module.VisitorsModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
